module.exports = [{"url":"/blog/2018/04/books/","year":"2018","month":"04","name":"books","title":"How to become a better developer (without coding)","titlePicture":"01.jpg","summary":"Some time ago I gave a talk about 15 books in 15 minutes. This is my personal list of must read books for developers. Yes, you will become a better developer by reading them. I even dare to say that at some point you will not become better by only practicing or learning new programming languages. But by reading books about our profession or even about the broader business.","author":"Sebastian Waschnick","authorEmail":"Sebastian.Waschnick@asideas.de","markdownUrl":"/blog/2018/04/books.md","permalink":"http://axelspringerideas.de/blog/2018/04/books/","id":"66fea07468b5196658d9fd7b19ab7d6a","date":"2016-11-01T11:00:00.000Z"},{"year":"2018","month":"03","name":"lazy-learning-per-knn","title":"Lazy Learning per KNN","titlePicture":"knn00.jpg","summary":"Künstliche Intelligenz gilt nach und nach immer weniger als Innovation und immer mehr als alltägliches Werkzeug. Um unsere Kunden optimal zu maschinellem Lernen beraten zu können, stellten wir uns die Frage, wie effizient es funktionieren würde, ein Empfehlungssystem für Nachrichtenartikel basierend auf trägem Lernen (\"lazy learning\") zu schreiben.","author":"Tarek Madany Mamlouk","authorEmail":"tarek.madanymamlouk@asideas.de","url":"/blog/2018/03/lazy-learning-per-knn/","markdownUrl":"/blog/2018/03/lazy-learning-per-knn.md","authorPictureUrl":"//www.gravatar.com/avatar/c37b21462586f4b3deafdaa80ef01503","permalink":"http://axelspringerideas.de/blog/2018/03/lazy-learning-per-knn/","id":"fcba38ec9accf5c59e06c2c8e6e73841","date":"2018-03-01T11:00:00.000Z"},{"year":"2018","month":"03","name":"knowledge-transfer-offsite-im-motionlab","title":"Knowledge-Transfer-Offsite im MotionLab Berlin","titlePicture":"kto00.jpg","summary":"Bei AS Ideas generieren wir kontinuierlich Ideen, Prototypen und Wissen. Erfolgskritisch ist dabei, dass wir unsere Initiativen reflektieren und das gewonnene Wissen untereinander teilen. Für einen freien Kopf und frische kreative Energie zogen wir uns diesen März ins Offsite zurück...","author":"Tarek Madany Mamlouk","authorEmail":"tarek.madanymamlouk@asideas.de","url":"/blog/2018/03/knowledge-transfer-offsite-im-motionlab/","markdownUrl":"/blog/2018/03/knowledge-transfer-offsite-im-motionlab.md","authorPictureUrl":"//www.gravatar.com/avatar/c37b21462586f4b3deafdaa80ef01503","permalink":"http://axelspringerideas.de/blog/2018/03/knowledge-transfer-offsite-im-motionlab/","id":"f7551fd2655c98432be48f66a1304e69","date":"2018-03-01T11:00:00.000Z"},{"year":"2018","month":"03","name":"immovation-days-bei-immowelt-in-hamburg","title":"Immovation Days bei Immowelt in Hamburg","titlePicture":"immo1.jpg","summary":"Immowelt veranstaltet in regelmäßigen Abständen den 'Immovation Day', einen Tag an dem die Entwickler die Freiheit bekommen, an Projekten zu arbeiten, die sie sich selbst überlegen und ohne Vorgaben umsetzen. Dieses mal mischten wir von AS Ideas mit und bauten Blockchains für Immobilien.","author":"Tarek Madany Mamlouk","authorEmail":"tarek.madanymamlouk@asideas.de","url":"/blog/2018/03/immovation-days-bei-immowelt-in-hamburg/","markdownUrl":"/blog/2018/03/immovation-days-bei-immowelt-in-hamburg.md","authorPictureUrl":"//www.gravatar.com/avatar/c37b21462586f4b3deafdaa80ef01503","permalink":"http://axelspringerideas.de/blog/2018/03/immovation-days-bei-immowelt-in-hamburg/","id":"3b93275581d56907785662604c0434ef","date":"2018-03-01T11:00:00.000Z"},{"year":"2018","month":"03","name":"ideas-engineering-praesentiert-die-ipa","title":"Ideas Engineering präsentiert die IPA","titlePicture":"ipa3.jpg","summary":"Alle zwei bis vier Wochen finden wir uns gemeinsam ein um uns die Geschichte darüber zu erzählen was wir in den vergangenen zwei Wochen gemacht haben. So steht es in der Scrum Bibel. Jedes Team bereitet sich auf diese Zeremonie ausführlich vor, unsere Zuhörer sind aber zumeist aus den eigenen Kreisen.","author":"Alexandra Nicolae","authorEmail":"alexandra.nicolae@asideas.de","url":"/blog/2018/03/ideas-engineering-praesentiert-die-ipa/","markdownUrl":"/blog/2018/03/ideas-engineering-praesentiert-die-ipa.md","authorPictureUrl":"//www.gravatar.com/avatar/4b6efb329eb483ed2a322e5ca60d2ac1","permalink":"http://axelspringerideas.de/blog/2018/03/ideas-engineering-praesentiert-die-ipa/","id":"28ea93b32d1da532b320efa712cbe039","date":"2018-03-01T11:00:00.000Z"},{"url":"/blog/2018/02/7-tipps-fuer-agiles-arbeiten/","year":"2018","month":"02","date":"2018-02-01T11:00:00.000Z","name":"7-tipps-fuer-agiles-arbeiten","title":"Ein kleiner Einstieg in Agilität: 7 Tipps für besseres Arbeiten","titlePicture":"cover.jpg","summary":"Unsere Welt verändert sich. Laufend passieren neue Sachen, neue Events, neue Momente, die uns dazu zwingen, unsere Arbeits- und Vorgehensweise anzupassen. Deshalb wird auch der Begriff des agilen Arbeitens immer wichtiger. Aber was ist agiles Arbeiten überhaupt?","author":"Sebastian Waschnick","authorEmail":"Sebastian.Waschnick@asideas.de","markdownUrl":"/blog/2018/02/7-tipps-fuer-agiles-arbeiten.md","permalink":"http://axelspringerideas.de/blog/2018/02/7-tipps-fuer-agiles-arbeiten/","id":"79c34d5026fb53a143b2e25b620f14f7"},{"draft":true,"url":"/blog/2017/06/axel-springer-tech-journey-takhles-balagan-and-chutzpah/","year":"2017","month":"06","date":"2017-06-01T10:00:00.000Z","name":"axel-springer-tech-journey-takhles-balagan-and-chutzpah","title":"Axel Springer Tech Journey - Takhles, Balagan and Chutzpah","titlePicture":"05-FRIDAY/20.jpg","summary":"For one week I had the honor and privilege to travel together with 22 other members of the Axel Springer Family to Israel. Together with me was one direct colleague, some people from the ASIT and people from all over Axel Springer: BILD, WELT, Axel Springer Spain, Media Impact, Aufeminin, immonet, immowelt, Axel Springer Ringe and Onet. It was awesome.","author":"Sebastian Waschnick","authorEmail":"Sebastian.Waschnick@asideas.de","markdownUrl":"/blog/2017/06/axel-springer-tech-journey-takhles-balagan-and-chutzpah.md","permalink":"http://axelspringerideas.de/blog/2017/06/axel-springer-tech-journey-takhles-balagan-and-chutzpah/","id":"9edb659c873947a22e51aa480fff7642"},{"year":"2017","month":"01","name":"aurelia-or-how-i-stopped-worrying-and-learned-to-love-spa-development","title":"Aurelia – or how I stopped worrying and learned to love spa development","titlePicture":"good-news-everyone-there-is-another-javascript-mv-framework-to-learn.jpg","summary":"Um einen aktuellen Einblick in die Frontend-Entwicklung zu erhalten, verweise ich auf die großartige Zusammenfassung “how it feels to learn JS in 2016“. Jede Woche ein neues Framework, hunderttausende von Paketen auf npmjs.org… Und jetzt noch ein neues Framework?","author":"Julian Godesa","authorEmail":"julian.godesa@asideas.de","url":"/blog/2017/01/aurelia-or-how-i-stopped-worrying-and-learned-to-love-spa-development/","markdownUrl":"/blog/2017/01/aurelia-or-how-i-stopped-worrying-and-learned-to-love-spa-development.md","authorPictureUrl":"//www.gravatar.com/avatar/73ef02d54f6834adf7e22363098ef800","permalink":"http://axelspringerideas.de/blog/2017/01/aurelia-or-how-i-stopped-worrying-and-learned-to-love-spa-development/","id":"9e4d4b1d4b91a3e6b1307a9a35d86792","date":"2017-01-01T11:00:00.000Z"},{"year":"2016","month":"11","name":"paper-prototyping","title":"Paper Prototyping","titlePicture":"8258201693_302a3d26b0_o.jpg","summary":"Prototyping erlaubt es schnell zu sichtbaren Ergebnissen und damit zu frühzeitigem direkten Feedback vom Kunden zu kommen. Dieses Feedback kann dann in einem agilen Loop wieder in den nächsten Prototyp integriert werden. Aber selbst Prototyping benötigt eine gewisse Zeit. Ein initiales Projektsetup muss erstellt werden sowie ein initialer Funktionsumfang erdacht und umgesetzt werden.","author":"Marco Böttcher","authorEmail":"marco.boettcher@asideas.de","url":"/blog/2016/11/paper-prototyping/","markdownUrl":"/blog/2016/11/paper-prototyping.md","authorPictureUrl":"//www.gravatar.com/avatar/5a14c8310cd28d2cc8df07ccfa7a77ad","permalink":"http://axelspringerideas.de/blog/2016/11/paper-prototyping/","id":"11403d5acb887d85abf26a3a835a349f","date":"2016-11-01T11:00:00.000Z"},{"year":"2016","month":"10","name":"du-programmierst-wie-ein-madchen","title":"Du programmierst wie ein Mädchen","titlePicture":"20161015_120153_HDR-02.jpg","summary":"Und wieder ein weiterer Hackathon, dieses mal waren wir aber als Firma, Sponsor und als Mentoren dort: Der von den Geek Girls Carrots veranstaltete “hack like a girl”. Natürlich auch für Männer. Es geht ja um Inklusion, nicht Exklusion. Insgesamt waren etwa 42 Teilnehmerinnen hier, darunter auch eine Handvoll Männer. Das Thema: Health & Fitness. Gastgeber war Axel Springer, wir haben den Unternehmerclub (ein Penthouse in der Nähe vom Judischen Museum) für die teilnehmenden IT-Nachwuchskräfte zur Verfügung gestellt.","author":"Sebastian Waschnick","authorEmail":"Sebastian.Waschnick@asideas.de","url":"/blog/2016/10/du-programmierst-wie-ein-madchen/","markdownUrl":"/blog/2016/10/du-programmierst-wie-ein-madchen.md","authorPictureUrl":"//www.gravatar.com/avatar/cc15aef428387a198e89b9f0c0c1a965","permalink":"http://axelspringerideas.de/blog/2016/10/du-programmierst-wie-ein-madchen/","id":"7ed1d91a622ea99e336547a48af45079","date":"2016-10-01T10:00:00.000Z"},{"year":"2016","month":"10","name":"coderetreat-irgendwer","title":"Coderetreat, irgendwer?","titlePicture":"Screen-Shot-2016-10-12-at-14.00.52.png","summary":"Am 22.10.2016 ist es wieder soweit: Der “Global Day of Coderetreat” findet wieder statt. Und ihr fragt euch jetzt sicherlich, was ist eigentlich ein Coderetreat. Denn so häufig hört man davon noch nicht bzw. den meisten Entwicklern, denen ich davon erzähle, haben noch nie nur von dem Wort gehört. Zeit das zu ändern!","author":"Sebastian Waschnick","authorEmail":"Sebastian.Waschnick@asideas.de","url":"/blog/2016/10/coderetreat-irgendwer/","markdownUrl":"/blog/2016/10/coderetreat-irgendwer.md","authorPictureUrl":"//www.gravatar.com/avatar/cc15aef428387a198e89b9f0c0c1a965","permalink":"http://axelspringerideas.de/blog/2016/10/coderetreat-irgendwer/","id":"c866908939b82bc2f1f64e942d70aa08","date":"2016-10-01T10:00:00.000Z"},{"year":"2016","month":"10","name":"7-reasons-why-you-should-go-to-a-hackathon","title":"7 reasons why you should go to a Hackathon","titlePicture":"01.jpeg","summary":"A hackathon is any event of any duration where people come together to solve problems with digital means and present them to each other. That’s it, really simple. In most cases a hackathon is on two days and you get 24 hours time for the hacking. But why should you even bother to invest two of your precious days? Well, here are at least seven reasons!","author":"Sebastian Waschnick","authorEmail":"Sebastian.Waschnick@asideas.de","url":"/blog/2016/10/7-reasons-why-you-should-go-to-a-hackathon/","markdownUrl":"/blog/2016/10/7-reasons-why-you-should-go-to-a-hackathon.md","authorPictureUrl":"//www.gravatar.com/avatar/cc15aef428387a198e89b9f0c0c1a965","permalink":"http://axelspringerideas.de/blog/2016/10/7-reasons-why-you-should-go-to-a-hackathon/","id":"1bf41f79c2dd8d752ac0c69bcb8387e1","date":"2016-10-01T10:00:00.000Z"},{"year":"2016","month":"09","name":"bed-con-2016-das-berliner-familientreffen-der-java-community","title":"BED-CON 2016 – Das Berliner Familientreffen der Java Community","titlePicture":"eingang.jpg","summary":"Das jährliche Familientreffen der Berliner Java Community hat wieder stattgefunden – die Berlin Expert Days, kurz einfach BED-CON genannt. Da die Konferenz in Berlin stattfindet und nur zwei Tage geht, gehört sie einfach zu den bequemsten Konferenzen für Berliner. Was man auch den Teilnehmern anmerkt: Bereits beim Ankommen erkennt man, dass dies eine IT-Konferenz ist. Es gibt nur männliche Softwareentwickler, vorzugsweise etwas älter und es wird nur deutsch gesprochen. Der Frauenanteil liegt bei etwa 2-4%. Schade eigentlich, andere Konferenzen zeigen, wie es besser geht.","author":"Sebastian Waschnick","authorEmail":"Sebastian.Waschnick@asideas.de","url":"/blog/2016/09/bed-con-2016-das-berliner-familientreffen-der-java-community/","markdownUrl":"/blog/2016/09/bed-con-2016-das-berliner-familientreffen-der-java-community.md","authorPictureUrl":"//www.gravatar.com/avatar/cc15aef428387a198e89b9f0c0c1a965","permalink":"http://axelspringerideas.de/blog/2016/09/bed-con-2016-das-berliner-familientreffen-der-java-community/","id":"516c6f6f73a3f922d663e3e625497464","date":"2016-09-01T10:00:00.000Z"},{"year":"2016","month":"08","name":"warum-agil-so-schwer-ist","title":"Warum agil so schwer ist","titlePicture":"9588038559_a670b73d29_o.jpg","summary":"Agil ist doch eigentlich ganz einfach. Ein großes Vorhaben wird in kleine Iterationen unterteilt; es wird immer das wichtigste umgesetzt;  das Schaffen von Wert für den Kunden steht im Vordergrund; Ziele und Fortschritt werden transparent gemacht; das Team organisiert sich selbst und bemüht sich um kontinuierliche Verbesserung.","author":"Sascha Korp","authorEmail":"sascha.korp@asideas.de","url":"/blog/2016/08/warum-agil-so-schwer-ist/","markdownUrl":"/blog/2016/08/warum-agil-so-schwer-ist.md","authorPictureUrl":"//www.gravatar.com/avatar/8eb88306892b12d6454d2d26e851eb5f","permalink":"http://axelspringerideas.de/blog/2016/08/warum-agil-so-schwer-ist/","id":"36435884a990985668782494f395444b","date":"2016-08-01T10:00:00.000Z"},{"year":"2016","month":"08","name":"social-day-2016-die-arche-hellersdorf","title":"Social Day 2016 @ Die Arche Hellersdorf","titlePicture":"20160722-084602-20160722-084602-P7220001.jpg","summary":"Dieses Jahr hatten wir unseren ersten Social Day, zusammen mit der Axel Springer IT. Mit insgesamt 55 Mitarbeitern sind wir mit einem Bus angereist, um Die Arche in Berlin-Hellersdorf zu unterstützen. Das ganze war freiwillig, jeder konnte sich dazu anmelden, wenn er wollte.","author":"Sebastian Waschnick","authorEmail":"Sebastian.Waschnick@asideas.de","url":"/blog/2016/08/social-day-2016-die-arche-hellersdorf/","markdownUrl":"/blog/2016/08/social-day-2016-die-arche-hellersdorf.md","authorPictureUrl":"//www.gravatar.com/avatar/cc15aef428387a198e89b9f0c0c1a965","permalink":"http://axelspringerideas.de/blog/2016/08/social-day-2016-die-arche-hellersdorf/","id":"2b2906816703fbedb90696a8d7875e26","date":"2016-08-01T10:00:00.000Z"},{"year":"2016","month":"08","name":"continuously-delivering-infrastructure-part-1-ansible-molecule-and-testinfrastructure","title":"Continuously delivering infrastructure – Part 1: Ansible, Molecule and Testinfrastructure","titlePicture":"TestAllTheThings.jpg","summary":"As part of a centralised system engineer tools and operations team, we often show teams how to set up their continuous integration workflow. Most of the time, this is build in a Java environment with a pipeline made in Jenkins CI and of course some testing frameworks.","author":"Sebastian Kornehl","authorEmail":"Sebastian.Kornehl@asideas.de","url":"/blog/2016/08/continuously-delivering-infrastructure-part-1-ansible-molecule-and-testinfrastructure/","markdownUrl":"/blog/2016/08/continuously-delivering-infrastructure-part-1-ansible-molecule-and-testinfrastructure.md","authorPictureUrl":"//www.gravatar.com/avatar/a74ad1edf5a1d8049b74749433954ac9","permalink":"http://axelspringerideas.de/blog/2016/08/continuously-delivering-infrastructure-part-1-ansible-molecule-and-testinfrastructure/","id":"0509572a83b5d2c879bf8358169990ac","date":"2016-08-01T10:00:00.000Z"},{"url":"/blog/2016/07/automatisierte-updates-von-versionen-in-maven-mittels-jenkins/","year":"2016","month":"07","name":"automatisierte-updates-von-versionen-in-maven-mittels-jenkins","title":"automatisierte-updates-von-versionen-in-maven-mittels-jenkins","titlePicture":"","summary":"","author":"","authorEmail":"","markdownUrl":"/blog/2016/07/automatisierte-updates-von-versionen-in-maven-mittels-jenkins.md","permalink":"http://axelspringerideas.de/blog/2016/07/automatisierte-updates-von-versionen-in-maven-mittels-jenkins/","id":"f3165258e59a07612328f0e9e871ce48","date":"2016-07-01T10:00:00.000Z"},{"year":"2016","month":"07","name":"42-stunden-bei-einem-hackathon","title":"42 Stunden bei einem Hackathon","titlePicture":"image2.JPG.jpeg","summary":"Hey! I’m Sebastian, a newly member of Hackerstolz and a huge fan of Hackathons. This is a short story about my last Hackathon. It was not your most typical Hackathon, as it was kind of a corporate one about new ideas some business units, but you still will get the idea how a Hackathon works.","author":"Sebastian Waschnick","authorEmail":"Sebastian.Waschnick@asideas.de","url":"/blog/2016/07/42-stunden-bei-einem-hackathon/","markdownUrl":"/blog/2016/07/42-stunden-bei-einem-hackathon.md","authorPictureUrl":"//www.gravatar.com/avatar/cc15aef428387a198e89b9f0c0c1a965","permalink":"http://axelspringerideas.de/blog/2016/07/42-stunden-bei-einem-hackathon/","id":"e1e29e70cd0fc9ff68df8581fae86997","date":"2016-07-01T10:00:00.000Z"},{"year":"2016","month":"06","name":"ideas-for-hackathons","title":"Ideas for Hackathons","titlePicture":"01.png","summary":"Hackathons machen enorm viel Spaß und man kann dabei mehr lernen als in mehreren Monaten normaler Arbeit. Wir waren dieses Jahr an zwei Wochenenden bei zwei Hackathons, hatten enorm viel Spaß und haben drei Preise gewonnen.","author":"Sebastian Waschnick","authorEmail":"Sebastian.Waschnick@asideas.de","url":"/blog/2016/06/ideas-for-hackathons/","markdownUrl":"/blog/2016/06/ideas-for-hackathons.md","authorPictureUrl":"//www.gravatar.com/avatar/cc15aef428387a198e89b9f0c0c1a965","permalink":"http://axelspringerideas.de/blog/2016/06/ideas-for-hackathons/","id":"dab267242813434b0f614cc59def080b","date":"2016-06-01T10:00:00.000Z"},{"url":"/blog/2018/05/wie-machen-wir-bei-ideas-engineering-eigentlich-recruiting/","year":"2018","month":"05","date":"2018-05-01T12:00:00.000Z","name":"wie-machen-wir-bei-ideas-engineering-eigentlich-recruiting","title":"Recruiting 2.0: Wie wir bei Ideas Engineering rekrutieren","titlePicture":"cover.jpg","summary":"Wir haben keine Team Leads, Abteilungsleiter und nur einen Manager: Unseren angesagten und hippen Michael Alber, den Geschäftsführer. In so einem Unternehmen, dass flache Hierarchie und den Geist des Lean Management lebt, kommt dem Recruiting, also der Einstellung von neuen Mitarbeitern, besondere Bedeutung zu. Nur wie macht man das jetzt konkret?","author":"Sebastian Waschnick","authorEmail":"Sebastian.Waschnick@asideas.de","markdownUrl":"/blog/2018/05/wie-machen-wir-bei-ideas-engineering-eigentlich-recruiting.md","permalink":"http://axelspringerideas.de/blog/2018/05/wie-machen-wir-bei-ideas-engineering-eigentlich-recruiting/","id":"84ac7c16b05668e61724c96e0db58677"}]

